import React, { Component } from 'react';

export class Projects extends Component {

     render() {
        let {pname,pdesp,time,lo} = this.props;
        console.log(lo);
        return (
                <>
                
                <div className="xl:w-1/3 md:w-1/2 p-4 mt-1 mb-1">
                <div className="bg-gray-800 p-6 rounded-lg">
                <img style={{border:"1px solid white",borderRadius:"5px"}} className="rounded w-full object-cover object-center mb-6" src={lo} alt="content"/>
                <h3 className="mb-2 tracking-widest text-gray-200 text-xs font-medium title-font">Duration : {time}</h3>
                <h2 className="text-lg text-gray-100 font-medium title-font mb-4">{pname}</h2>
                <hr className='mb-3'></hr>
                <p className="leading-relaxed text-base text-gray-200">{pdesp.slice(0,70)}...</p>
                </div>
                </div>
          
                </>
        )
    }
}

export default Projects


